// app/pages/tutor-documents.tsx
"use client";
import React, { useEffect, useState } from "react";
import { useSearchParams } from "next/navigation";
import { Card, CardHeader, CardContent, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { FileText, Download, RefreshCw, CheckCircle, XCircle, Clock } from "lucide-react";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Badge } from "@/components/ui/badge";

interface Document {
  idPFE: number;
  idLivrable: number;
  fichier: string;
  nomGroupe: string;
  validationStatus?: string;
  comment?: string;
}

export default function TutorDocumentsPage() {
  const searchParams = useSearchParams();
  const idTuteur = searchParams.get("idTuteur") || "";
  const [documents, setDocuments] = useState<Document[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchDocuments = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(`/api/tutor/group-documents?idTuteur=${idTuteur}`);
      if (response.ok) {
        const data = await response.json();
        setDocuments(data);
      } else {
        setError("Failed to fetch documents");
      }
    } catch (err) {
      setError("Error fetching documents");
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchDocuments();
  }, [idTuteur]);

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "validated":
        return <CheckCircle className="h-4 w-4 text-green-500" />;
      case "rejected":
        return <XCircle className="h-4 w-4 text-red-500" />;
      default:
        return <Clock className="h-4 w-4 text-amber-500" />;
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case "validated":
        return "Validé";
      case "rejected":
        return "Rejeté";
      default:
        return "En attente";
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return (
      <Alert variant="destructive">
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  return (
    <div className="container mx-auto py-8 px-4">
      <Card className="w-full max-w-3xl mx-auto">
        <CardHeader>
          <CardTitle>Documents du Groupe</CardTitle>
          <CardDescription>Documents soumis par les étudiants</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {documents.map((doc) => (
              <Card key={`${doc.idPFE}-${doc.idLivrable}`} className="p-4">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <FileText className="h-5 w-5" />
                    <span>{doc.nomGroupe}</span>
                    <Badge variant="secondary">Soumis</Badge>
                  </div>
                  <Button variant="ghost" size="sm" asChild>
                    <a href={`http://localhost:5000${doc.fichier}`} download>
                      <Download className="h-4 w-4 mr-2" />
                      Télécharger
                    </a>
                  </Button>
                </div>
                {doc.validationStatus && (
                  <div className="mt-3">
                    <div className="flex items-center gap-2">
                      {getStatusIcon(doc.validationStatus)}
                      <span className="font-medium">Tuteur</span>
                      <Badge variant="outline" className="ml-auto">
                        {getStatusText(doc.validationStatus)}
                      </Badge>
                    </div>
                    {doc.comment && <p className="text-sm mt-1 pl-6">{doc.comment}</p>}
                  </div>
                )}
              </Card>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}